export default {
  namespaced: true,
  state: {
    collections: [],
    collection: ''
  },
  getters: {
    collections: state => state.collections,
    collection: state => state.collection
  },
  mutations: {
    set: (state, { type, data }) => {
      const keys = Object.keys(state);
      for (let i = 0; i < keys.length; i++) {
        state[keys[i]] = keys[i] === type ? data : state[keys[i]];
      }
      return state;
    }
  }
};



