<template>
  <div id="dba">
    <div id="content">
      <div class="db-list">
        <h3 class="heading">Database Collections</h3>
        <hr>
        <ul>
          <li @click="selectCollection(collection)" v-for="(collection, index) in collections" :key="index">
            {{collection}}
          </li>
        </ul>
      </div>
      <div class="db-view">
        <div id="header">
          <div class="collection-name">
            <h3>{{collection}}</h3>
          </div>
          <div class="split">
            <button @click="toggleActiveUI">
              Switch to {{ACTIVE_UI === 'QUERY' ? 'Aggregation' : 'Query'}} Mode
            </button>
          </div>
        
        </div>
        <router-view :ACTIVE_UI="ACTIVE_UI"></router-view>
      </div>
    </div>
  </div>
</template>
<script>
  let vm;
  import { mapGetters, mapMutations } from 'vuex';
  import axios from 'axios';
  import { BASE_URL } from '../utils';
  
  export default {
    data: () => ({
      ACTIVE_UI: 'QUERY'
    }),
    computed: {
      ...mapGetters('app', ['collection', 'collections'])
    },
    methods: {
      selectCollection(collection) {
        vm.set({
          type: 'collection',
          data: collection
        });
        if (vm.$route.path !== '/' + collection) {
          vm.$router.push({
            name: 'Collection',
            params: {
              collection
            }
          });
        }
      },
      toggleActiveUI() {
        if (vm.ACTIVE_UI === 'QUERY') {
          vm.ACTIVE_UI = 'AGGREGATION';
        } else {
          vm.ACTIVE_UI = 'QUERY';
        }
      },
      ...mapMutations('app', ['set']),
      listCollections() {
        axios.get(`${BASE_URL}/list`)
          .then(res => {
            const { data: response } = res;
            console.log(response, ' response.');
            let collections = [];
            if (response.success) {
              const { data } = response;
              data.forEach(item => {
                collections.push(item.name);
              });
            }
            vm.set({
              type: 'collections',
              data: collections
            });
            collections = [];
          });
      }
    },
    created() {
      vm = this;
      vm.listCollections();
    }
  };
</script>
<style>
  #header {
    top: 0;
    left: 0;
    right: 0;
    height: 40px;
    background: #06150C;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    padding: 10px;
    color: white;
    width: 100%;
    margin-bottom: 20px;
  }
  
  #content {
    position: fixed;
    height: 100%;
    width: 100%;
    bottom: 0;
    background: ghostwhite;
    right: 0;
    left: 0;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
  }
  
  .heading {
    color: rgb(90, 194, 113);
  }
  
  
  .collection-name {
    display: block;
    width: 250px;
    float: left;
  }
  
  .commander {
    left: 0;
    /*position: absolute;*/
    top: 60px;
    width: 100%;
    right: 0;
  }
  
  .operations {
    /*position: absolute;*/
    left: 0;
    right: 0;
    width: 100%;
    z-index: 9;
  }
  
  .split {
    position: relative;
    float: left;
    display: block;
    z-index: 900;
  }
  
  .db-view {
    padding: 0;
    height: 100%;
    width: 100%;
    right: 0;
    top: 0;
    bottom: 0;
    background: black;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
  }
  
  .db-list ul li {
    cursor: pointer;
  }
  
  
  .db-list {
    width: 17%;
    display: block;
    background: #1D1B1B;
    color: white;
    bottom: 0;
    top: 0;
    height: 100%;
    padding: 15px;
    box-shadow: 2px 2px 3px 1px rgba(179, 173, 173, 0.24);
  }
  
  #logo {
    background: white;
    z-index: 6;
    bottom: 0;
    left: 0;
    right: 0;
    display: block;
    width: 267px;
    height: 51px;
    position: fixed;
  }
  
  img#root-parent {
    left: 0;
    position: absolute;
    bottom: 10px;
    width: 130px;
    margin: 0 auto;
    right: 0;
  }
</style>



