import Vue from 'vue';
import Router from 'vue-router';
import Collections from '../views/Collections';
import Collection from '../views/Collection';

Vue.use(Router);

export default new Router({
  hashbang: false,
  history: true,
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Collections',
      components: {
        default: Collections
      },
      children: [
        {
          path: '/:collection',
          name: 'Collection',
          component: Collection
        }
      ]
    }
  ]
});



