<template>
  <div class="collections-root">
    <div class="operations-command" :class="{'expanded': ACTIVE_UI === 'AGGREGATION'}">
      <div class="commander" v-if="ACTIVE_UI === 'AGGREGATION'">
        <Editor class="command-entry"
                v-model="payload.pipeline"
                @init="editorInit"
                lang="javascript"
                width="100%"
                height="400px"
                theme="twilight"/>
      </div>
      <div class="operations" v-if="ACTIVE_UI === 'QUERY'">
        <input class="input" id="filters" type="text" v-model="payload.filters" placeholder="Filters">
        <input class="input" type="text" id="projection" v-model="payload.projection" placeholder="Projection">
        <input class="input" type="text" id="skip" v-model="payload.skip" placeholder="Skip">
        <input class="input" type="text" id="limit" v-model="payload.limit" placeholder="Limit">
        <input class="input" type="text" id="sort" v-model="payload.sort" placeholder="Sort">
      </div>
    </div>
    <hr>
    <div id="command-button-group">
      <button class="button" @click="submitContent">
        Go
      </button>
      <button class="button" @click="clearAll">
        Clear
      </button>
    </div>
    <div class="results-output">
      <JsonViewer width="inherit" :value="result" copyable sort></JsonViewer>
    </div>
  </div>
</template>
<script>
  let vm;
  import Editor from 'vue2-ace-editor';
  import { mapGetters } from 'vuex';
  import axios from 'axios';
  import { BASE_URL } from '../utils';
  
  export default {
    name: 'Collection',
    props: {
      ACTIVE_UI: {
        required: true,
        type: String,
        validator(value) {
          // The value must match one of these strings
          return ['AGGREGATION', 'QUERY'].indexOf(value) !== -1;
        }
      }
    },
    components: {
      Editor
    },
    computed: {
      ...mapGetters('app', ['collection'])
    },
    data: () => ({
      payload: {
        filters: '',
        projection: '',
        sort: '',
        limit: '',
        skip: '',
        pipeline: '',
        collectionName: ''
      },
      result: {}
    }),
    methods: {
      editorInit() {
        require('brace/ext/language_tools'); //language extension pre-requsite...
        require('brace/mode/javascript');    //language
        require('brace/mode/less');
        require('brace/theme/twilight');
        require('brace/snippets/javascript'); //snippet
      },
      submitContent() {
        vm.payload.collectionName = vm.collection;
        
        axios.post(`${BASE_URL}/${String(vm.ACTIVE_UI)
          .toLowerCase()}`, vm.payload)
          .then(res => {
            const { data: response } = res;
            console.log(response, ' response.');
            if (response.success) {
              const { data } = response;
              vm.result = data;
            }
          });
      },
      clearAll() {
        vm.payload.pipeline = '';
        vm.payload.filters = '';
        vm.payload.sort = '';
        vm.payload.skip = '';
        vm.payload.limit = '';
        vm.payload.projection = '';
      },
    },
    created() {
      vm = this;
    }
  };
</script>
<style>
  .input {
    height: 24px !important;
    border-radius: 17px;
    width: 500px;
    border: 1px solid rgb(191, 191, 190);
    padding: 3px 11px;
    margin: 2px;
    background: white !important;
    outline: none;
  }
  
  .results-output {
    background: black;
    color: white;
    height: 100%;
    /* position: absolute; */
    width: 100%;
    left: 0;
    margin-top: 15px;
  }
  
  #skip, #limit {
    width: 100px;
  }
  
  .expanded {
    min-height: 335px;
    background: none !important;
    padding: 0 !important;
  }
  
  .split button {
    height: 40px;
    border-radius: 19px;
    outline: none;
    border: 1px;
    right: 25px;
    position: fixed;
    top: 9px;
  }
  
  .operations-command {
    margin-top: -20px;
    padding: 10px 5px;
    background: #777777EB;
    width: 100%;
    display: block;
  }
  
  .collections-root {
    width: 100%;
  }
</style>



